//
//

import { useState, useEffect } from 'react';
import { Player, Card } from '../utils/deck';
import { CardWithIndex, JudgeResult, PreviousTurnResult, BattleResult } from '../types/game';




interface DrawResult {
  updatedPlayers: Player[];
  updatedDeck: Card[];
  drawStatus: string;
}

interface UseTurnFlowProps {
  roundResult: string | null;
  setRoundResult: React.Dispatch<React.SetStateAction<string | null>>;
  nextMultiplier: number;
  setNextMultiplier: React.Dispatch<React.SetStateAction<number>>;
  fieldCards: (Card | null)[];
  setFieldCards: React.Dispatch<React.SetStateAction<(Card | null)[]>>;
  players: Player[];
  setPlayers: React.Dispatch<React.SetStateAction<Player[]>>;
  deck: Card[];
  setDeck: React.Dispatch<React.SetStateAction<Card[]>>;
  turnCount: number;
  setTurnCount: React.Dispatch<React.SetStateAction<number>>;
  jokerCount: number;
  setJokerCount: React.Dispatch<React.SetStateAction<number>>;
  setTurnIndex: number;
  setSetTurnIndex: React.Dispatch<React.SetStateAction<number>>;
  jokerDealtThisSet: boolean;
  setJokerDealtThisSet: React.Dispatch<React.SetStateAction<boolean>>;
  setLastRoundWarning: React.Dispatch<React.SetStateAction<boolean>>;
  setGameOver: React.Dispatch<React.SetStateAction<boolean>>;
  setGameOverReason: React.Dispatch<React.SetStateAction<string>>;
  setFeeCollected: React.Dispatch<React.SetStateAction<boolean>>;
  setPreviousTurnResult: React.Dispatch<React.SetStateAction<PreviousTurnResult | null>>;
  judgeWinner: (cards: CardWithIndex[]) => JudgeResult;
  determineWinnerAndLoser: (
    fieldCards: (Card | null)[],
    isDraw: boolean,
    winnerIndexes: number[],
    isReverse: boolean,
    originalWinnerIndex?: number
  ) => BattleResult | null;
  calculateScore: (
    winnerCard: Card,
    loserCard: Card,
    multiplier: number,
    isReverse: boolean
  ) => number;
  rankToValue: (card: Card) => number;
  drawCardsForNextTurn: (
    deck: Card[],
    players: Player[],
    createDeck: () => Card[],
    shuffleDeck: (deck: Card[]) => Card[],
    jokerDealtThisSet: boolean
  ) => DrawResult;
  checkJokerInHands: (players: Player[]) => boolean;
  checkGameEnd: (
    isSetEnd: boolean,
    jokerCount: number,
    players: Player[]
  ) => { shouldEnd: boolean; reason?: string };
  shouldReshuffleAfterSet: (jokerDealtThisSet: boolean) => boolean;
  createDeck: () => Card[];
  shuffleDeck: (deck: Card[]) => Card[];
  WAIT_TIME_MS: number;
}

interface UseTurnFlowReturn {
  currentMultiplier: number;
  setCurrentMultiplier: React.Dispatch<React.SetStateAction<number>>;
}

/**
 * 
 * 
 */
export function useTurnFlow({
  roundResult,
  setRoundResult,
  nextMultiplier,
  setNextMultiplier,
  fieldCards,
  setFieldCards,
  players,
  setPlayers,
  deck,
  setDeck,
  turnCount,
  setTurnCount,
  jokerCount,
  setJokerCount,
  setTurnIndex,
  setSetTurnIndex,
  jokerDealtThisSet,
  setJokerDealtThisSet,
  setLastRoundWarning,
  setGameOver,
  setGameOverReason,
  setFeeCollected,
  setPreviousTurnResult,
  judgeWinner,
  determineWinnerAndLoser,
  calculateScore,
  rankToValue,
  drawCardsForNextTurn,
  checkJokerInHands,
  checkGameEnd,
  shouldReshuffleAfterSet,
  createDeck,
  shuffleDeck,
  WAIT_TIME_MS,
}: UseTurnFlowProps): UseTurnFlowReturn {
  const [currentMultiplier, setCurrentMultiplier] = useState(1);

  // 
  useEffect(() => {
    if (roundResult !== null) {
      const timer = setTimeout(() => {
        console.log('[useTurnFlow] ');

        const allHandsEmpty = players.every((p) => p.hand.length === 0);

        console.log('[useTurnFlow] allHandsEmpty:', allHandsEmpty);
        console.log('[useTurnFlow] jokerCount:', jokerCount);
        console.log('[useTurnFlow] players:', players.map((p) => ({ name: p.name, points: p.points })));

        const cardsWithIndex: CardWithIndex[] = fieldCards.map((card, idx) => ({
          ...card!,
          playerIndex: idx,
        }));

        const result = judgeWinner(cardsWithIndex);
        const { winnerIndexes, isDraw, isReverse, originalWinnerIndex } = result;

        let scoreToAdd = 0;
        let winnerIdx = -1;
        let loserIdx = -1;

        const battleResult = determineWinnerAndLoser(
          fieldCards,
          isDraw,
          winnerIndexes,
          isReverse ?? false,
          originalWinnerIndex ?? undefined
        );

        if (battleResult) {
          const { winnerIndex, loserIndex, winnerCard, loserCard } = battleResult;

          console.log('[useTurnFlow] ===  ===');
          console.log('[useTurnFlow] :', turnCount + 1);
          console.log('[useTurnFlow] :', winnerCard.rank, ':', rankToValue(winnerCard));
          console.log('[useTurnFlow] :', loserCard.rank, ':', rankToValue(loserCard));
          console.log('[useTurnFlow] currentMultiplier:', currentMultiplier);
          console.log('[useTurnFlow] (judgeWinner):', isReverse);
          console.log('[useTurnFlow] (battleResult):', battleResult.isReverse);

          scoreToAdd = calculateScore(winnerCard, loserCard, currentMultiplier, isReverse || false);
          winnerIdx = winnerIndex;
          loserIdx = loserIndex;

          console.log('[useTurnFlow] :', scoreToAdd);
          console.log('[useTurnFlow] :', winnerIdx, ':', loserIdx);
        }

        // 
        let isSetEnd = false;

        // 
        if (allHandsEmpty) {
          console.log('[useTurnFlow]  - ');
          console.log('[useTurnFlow] setTurnIndex:', setTurnIndex);
          console.log('[useTurnFlow] jokerDealtThisSet:', jokerDealtThisSet);

          if (setTurnIndex === 4) {
            isSetEnd = true;
            console.log('[useTurnFlow] isSetEndtrue');
            setCurrentMultiplier(1);
            setNextMultiplier(1);
            setSetTurnIndex(0);

            if (shouldReshuffleAfterSet(jokerDealtThisSet)) {
              console.log('[useTurnFlow] JOKER +1 ');
              setJokerCount((prev) => {
                const newCount = prev + 1;
                console.log('[useTurnFlow] jokerCountprev=', prev, 'new =', newCount);
                setTimeout(() => {
                  const gameEndCheck = checkGameEnd(true, newCount, players);
                  if (gameEndCheck.shouldEnd) {
                    console.log('[useTurnFlow] :', gameEndCheck.reason);
                    setGameOverReason( gameEndCheck.reason || 'JOKER');
                    setGameOver(true);
                  }
                }, 0);
                return newCount;
              });
            } else {
              console.log('[useTurnFlow] JOKERjokerDealtThisSet = false');
            }
          } else {
            console.log('[useTurnFlow] ');
            setCurrentMultiplier(nextMultiplier);
            setSetTurnIndex((i) => i + 1);
          }
        } else {
          console.log('[useTurnFlow]  - ');
          console.log('[useTurnFlow] setTurnIndex:', setTurnIndex);

          if (setTurnIndex === 4) {
            console.log('[useTurnFlow] WARNING: setTurnIndex4');
            setCurrentMultiplier(1);
            setNextMultiplier(1);
            setSetTurnIndex(0);
          } else {
            setCurrentMultiplier(nextMultiplier);
            setSetTurnIndex((i) => i + 1);
          }
        }

        // 
        setFieldCards([null, null, null]);
        setTurnCount((c) => c + 1);
        setRoundResult(null);
        setFeeCollected(false);

        // 
        setPlayers((prevPlayers) => {
          let updated = [...prevPlayers];
          
          // 
          if (winnerIdx !== -1 && loserIdx !== -1 && scoreToAdd > 0) {
            console.log('[useTurnFlow]  :', updated[winnerIdx].points);
            console.log('[useTurnFlow]  :', updated[loserIdx].points);

            updated[winnerIdx] = {
              ...updated[winnerIdx],
              points: updated[winnerIdx].points + scoreToAdd,
            };
            updated[loserIdx] = {
              ...updated[loserIdx],
              points: updated[loserIdx].points - scoreToAdd,
            };

            console.log('[useTurnFlow]  :', updated[winnerIdx].points);
            console.log('[useTurnFlow]  :', updated[loserIdx].points);

            // 
            if (battleResult) {
            if (winnerIdx !== -1 && loserIdx !== -1) {
            setPreviousTurnResult({
            winnerIndex: winnerIdx,
            loserIndex: loserIdx,
            isDraw: false,
             });
            }
        } else {
            setPreviousTurnResult({
                winnerIndex: -1,
                loserIndex: -1,
                isDraw: true,
            });
        }


            // 
            const bankruptCheck = checkGameEnd(false, jokerCount, updated);
            if (bankruptCheck.shouldEnd) {
              console.log('[useTurnFlow] :', bankruptCheck.reason);
              setGameOverReason( bankruptCheck.reason || '');
              setGameOver(true);
              return updated;
            }
          }

          // 
          const { updatedPlayers, updatedDeck, drawStatus } = drawCardsForNextTurn(
            deck,
            updated,
            createDeck,
            shuffleDeck,
            jokerDealtThisSet
          );

          // JOKER
          const jokerInNewHands = checkJokerInHands(updatedPlayers);
          console.log('[useTurnFlow] JOKER:', jokerInNewHands);
          console.log('[useTurnFlow] jokerDealtThisSet:', jokerDealtThisSet);
          console.log('[useTurnFlow] isSetEnd:', isSetEnd);

          // OR
          if (isSetEnd) {
            setJokerDealtThisSet(jokerInNewHands);
            console.log('[useTurnFlow] :', jokerInNewHands);
          } else {
            setJokerDealtThisSet((prev) => {
              const newValue = prev || jokerInNewHands;
              console.log(
                '[useTurnFlow] OR: prev =',
                prev,
                ', jokerInNewHands =',
                jokerInNewHands,
                ' newValue =',
                newValue
              );
              return newValue;
            });
          }

          setDeck(updatedDeck);
          setLastRoundWarning(drawStatus === 'warn');

          return updatedPlayers;
        });
      }, WAIT_TIME_MS);

      return () => clearTimeout(timer);
    }
  }, [
    roundResult,
    currentMultiplier,
    nextMultiplier,
    deck,
    jokerDealtThisSet,
    setTurnIndex,
    turnCount,
    fieldCards,
    jokerCount,
    players,
    setRoundResult,
    setNextMultiplier,
    setFieldCards,
    setPlayers,
    setDeck,
    setTurnCount,
    setJokerCount,
    setSetTurnIndex,
    setJokerDealtThisSet,
    setLastRoundWarning,
    setGameOver,
    setFeeCollected,
    setPreviousTurnResult,
    judgeWinner,
    determineWinnerAndLoser,
    calculateScore,
    rankToValue,
    drawCardsForNextTurn,
    checkJokerInHands,
    checkGameEnd,
    shouldReshuffleAfterSet,
    createDeck,
    shuffleDeck,
    WAIT_TIME_MS,
  ]);

  return {
    currentMultiplier,
    setCurrentMultiplier,
  };
}
