//WebSocket

//onlinesrc/context/SocketContext.tsx



import React, { createContext, useContext, useEffect, useState, ReactNode } from 'react';
import { io, Socket } from 'socket.io-client';


interface SocketContextType {
  socket: Socket | null;
  isConnected: boolean;
}


const SocketContext = createContext<SocketContextType>({
  socket: null,
  isConnected: false,
});

interface SocketProviderProps {
  children: ReactNode;
}




export const SocketProvider: React.FC<SocketProviderProps> = ({ children }) => {
  const [socket, setSocket] = useState<Socket | null>(null);
  const [isConnected, setIsConnected] = useState(false);

  useEffect(() => {
    console.log('[SocketContext] ');
    const newSocket = io('https://kadoma.onrender.com',{
      transports: ['websocket'],
      reconnection: true,
      reconnectionAttempts:10,
      reconnectionDelay:3500,
      reconnectionDelayMax: 10000,
      timeout:120000,
    });
    
    newSocket.on('connect', () => {
      console.log('[SocketContext] :', newSocket.id);
      setIsConnected(true);
      setSocket(newSocket);
    });
   


    newSocket.on('disconnect', () => {
      console.log('[SocketContext] ');
      setIsConnected(false);
    });

    newSocket.on('reconnect', (attemptNumber) => {
    console.log('[SocketContext] :', attemptNumber);
    //: 
  const savedRoomId = localStorage.getItem('kadoma_active_room');
  const userId = localStorage.getItem('kadoma_user_id');
  
  if (savedRoomId && userId) {
    console.log('[SocketContext] Auto-rejoin after reconnect');
    console.log('[SocketContext] roomId:', savedRoomId, 'userId:', userId);
    
    // 1
    setTimeout(() => {
      console.log('[SocketContext] Emitting rejoin_game');
      newSocket.emit('rejoin_game', { 
        roomId: savedRoomId,
        userId: userId
      });
    }, 1000);
  } else {
    console.log('[SocketContext] No saved room or userId, skipping auto-rejoin');
  }
    });

    newSocket.on('reconnect_attempt', (attemptNumber) => {
    console.log('[SocketContext] :', attemptNumber);
    });

    newSocket.on('reconnect_failed', () => {
    console.error('[SocketContext] ');
    });


    //
    newSocket.on('connect_error', (error) => {
      console.error('[SocketContext] :', error);
      setIsConnected(false);
    });

    //rejoin_failed reconnect_failed 
    newSocket.on('rejoin_failed', (data) => {
      console.log('[SocketContext] :', data.message);
      localStorage.removeItem('kadoma_active_room');
      
      alert(`: ${data.message}\n`);
      
    });



     // 
    return () => {
      console.log('[SocketContext] ');
      newSocket.close();
    };
  }, []);

  return (
    <SocketContext.Provider value={{ socket, isConnected }}>
      {children}
    </SocketContext.Provider>
  );
}
export const useSocket = () => {
  const context = useContext(SocketContext);
  if (context === undefined) {
    throw new Error('useSocket must be used within SocketProvider');
  }
  return context;
};