// src/online/hooks/useRoundJudge.ts
// round_result

import { useState, useEffect } from 'react';
import { Socket } from 'socket.io-client';

interface UseRoundJudgeProps {
  socket: Socket | null;
}

interface UseRoundJudgeReturn {
  roundResult: string | null;
  scores: number[];
  wins: number[];
  isShowdown: boolean;
}

export function useRoundJudge({ socket }: UseRoundJudgeProps): UseRoundJudgeReturn {
  const [roundResult, setRoundResult] = useState<string | null>(null);
  const [scores, setScores] = useState<number[]>([0, 0, 0]);
  const [wins, setWins] = useState<number[]>([0, 0, 0]);
  const [isShowdown, setIsShowdown] = useState(false);

  useEffect(() => {
    if (!socket) return;

    console.log('[useRoundJudge] Setting up event listeners');

    //cards_revealed 
    socket.on('cards_revealed', () => {
      console.log('[useRoundJudge] cards_revealed - ');
      setIsShowdown(true);
    });

    //
    socket.on('game_start', (data) => {
      console.log('[useRoundJudge] game_start - :', data.scores);
      setScores(data.scores);
      setWins([0, 0, 0]); 
      setIsShowdown(false);
    });

   

    // 
    socket.on('round_result', (data) => {
      console.log('[useRoundJudge] round_result received:', data);
      setRoundResult(data.message);
      setScores(data.scores);
      setWins(data.wins);
      
      // 2
      setTimeout(() => {
        setRoundResult(null);
        setIsShowdown(false);
      }, 2000);
    });

    //
    socket.on('turn_update', (data) => {
      console.log('[useRoundJudge] turn_update - :', data.scores);
      setScores(data.scores);
      setIsShowdown(false);
    });

    // 
    return () => {
      console.log('[useRoundJudge] Cleaning up event listeners');
      socket.off('game_start');
      socket.off('cards_revealed');
      socket.off('round_result');
      socket.off('turn_update');
    };
  }, [socket]);

  return {
    roundResult,
    scores,
    wins,
    isShowdown,
  };
}