// src/contexts/AuthContext.tsx
import React, { createContext, useContext, useState, useEffect } from 'react';
import { Socket } from 'socket.io-client';
import { useSocket } from './SocketContext';

interface User {
  id: string;
  username: string;
  currency: number;
  transferCode: string;
  playerId?:number;
}

interface AuthContextType {
  user: User | null;
  isLoading: boolean;
  updateUserCurrency: (amount: number) => void;
  isMaintenanceMode: boolean;
  login: (user: User) => void;
  logout: () => void;
  register: (username: string) => Promise<{ success: boolean; error?: string }>;
  loginWithCode: (code: string) => Promise<{ success: boolean; error?: string }>;
  updateCurrency: (newCurrency: number) => void;
}

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export function AuthProvider({ children }: { children: React.ReactNode }) {
  const { socket, isConnected } = useSocket();
  const [user, setUser] = useState<User | null>(null);
  const [isLoading, setIsLoading] = useState(true);
  const [isMaintenanceMode, setIsMaintenanceMode] = useState(false);


  //
  useEffect(() => {
    if (!socket) return;
    const handleMaintenanceMode = (data: any) => {
      console.log('[AuthContext] :', data.message);
      setIsMaintenanceMode(true);
      setIsLoading(false);
    };
    socket.on('maintenance_mode', handleMaintenanceMode);
    return () => {
      socket.off('maintenance_mode', handleMaintenanceMode);
    };
  }, [socket]);

  // : 


  //
  useEffect(() => {
    console.log('[AuthContext] useEffect , socket:', socket, 'isConnected:', isConnected);
    
    if (!socket || !isConnected) {
      console.log('[AuthContext] socket or isConnected is false, waiting...');
      return;
    }
     //
    if (isMaintenanceMode) {
      console.log('[AuthContext] ');
      setIsLoading(false);
      return;
    }



    const userId = localStorage.getItem('kadoma_user_id');
    
    if (userId) {
      console.log('[] :', userId);
    
      
    
      const timeout = setTimeout(() => {
       
      console.log('[] ');
      setIsLoading(false);
        
    }, 90000);
      socket.emit('auto_login', { userId }, (response: any) => {
        clearTimeout(timeout);

        if (response.success) {
          console.log('[] :', response.user);
          setUser(response.user);
          localStorage.setItem('kadoma_user_id', response.user.id);
          setIsLoading(false);
        } else {
          console.log('[] ');
          localStorage.removeItem('kadoma_user_id');
          setIsLoading(false);
        }
      });

    } else {
      console.log('[] ');
      setIsLoading(false);
    }
  }, [socket?.id, isConnected, isMaintenanceMode]);


  // 
  const login = (userData: User) => {
    console.log('[] :', userData);
    setUser(userData);
    localStorage.setItem('kadoma_user_id', userData.id);
  };

  // 
  const logout = () => {
    console.log('[] ');
    setUser(null);
    localStorage.removeItem('kadoma_user_id');
  };

  // 
  const register = async (username: string): Promise<{ success: boolean; error?: string }> => {
    
    console.log('[AuthContext] register :', username);
    console.log('[AuthContext] socket:', socket);
    
    
    if (!socket) {
      console.error('[AuthContext] socket is null!');
      return { success: false, error: '' };
    }

    console.log('[AuthContext] socket.emit("register") ');

    return new Promise((resolve) => {
      socket.emit('register', { username }, (response: any) => {
        console.log('[AuthContext] register callback :', response);
        if (response.success) {
          console.log('[AuthContext] login ');
          login(response.user);
          resolve({ success: true });
        } else {
          console.log('[AuthContext] :', response.error);
          resolve({ success: false, error: response.error });
        }
      });
    });
  };

  // 
  const loginWithCode = async (code: string): Promise<{ success: boolean; error?: string }> => {
    
    console.log('[AuthContext] loginWithCode :', code);

    if (!socket) {
      console.error('[AuthContext] socket is null!');
      return { success: false, error: '' };
    }

    console.log('[AuthContext] socket.emit("login_with_code") ');

    return new Promise((resolve) => {
      socket.emit('login_with_code', { transferCode: code }, (response: any) => {
        console.log('[AuthContext] loginWithCode callback :', response);
        if (response.success) {
          login(response.user);
          resolve({ success: true });
        } else {
          resolve({ success: false, error: response.error });
        }
      });
    });
  };


    //game_over  currency 
  useEffect(() => {
    if (!socket || !user) return;
    const handleGameOver = (data: any) => {
      console.log('[AuthContext] game_over received:', data);
      
      //  userId  currency 
      if (data.updatedCurrencies && data.updatedCurrencies[user.id]) {
        const newCurrency = data.updatedCurrencies[user.id];
        console.log('[AuthContext] Updating currency:', newCurrency);
        
        setUser(prev => prev ? { ...prev, currency: newCurrency } : null);
      }
    };
    socket.on('game_over', handleGameOver);
    return () => {
      socket.off('game_over', handleGameOver);
    };
  }, [socket, user?.id]);

  const updateCurrency = (newCurrency: number) => {
    if (user) {
      setUser({ ...user, currency: newCurrency });
      console.log('[AuthContext] Currency updated:', newCurrency);
    }
  };


  // 
  const updateUserCurrency = (amount: number) => {
    if (!user) return;
    
    setUser({
      ...user,
      currency: user.currency + amount
    });
    
    // localStorage
    const updatedUser = {
      ...user,
      currency: user.currency + amount
    };
    localStorage.setItem('kadoma_user', JSON.stringify(updatedUser));
  };

  

  return (
    <AuthContext.Provider value={{ user, isLoading, isMaintenanceMode, login, logout, register, loginWithCode, updateCurrency, updateUserCurrency }}>
      {children}
    </AuthContext.Provider>
  );
}

export function useAuth() {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within AuthProvider');
  }
  return context;
}