// src/online/components/OnlineGame.tsx
//UI

import { useState, useEffect } from 'react'; 
import { useSocket } from '../contexts/SocketContext';
import { useAuth } from '../contexts/AuthContext'; 
import { useOnlineGameState } from '../hooks/useOnlineGameState';
import { useRoundJudge } from '../hooks/useRoundJudge';
import { useTurnFlow } from '../hooks/useTurnFlow';
import { useCardPlay } from '../hooks/useCardPlay';
import { useWarnings } from '../hooks/useWarnings';
import { useDisconnectNotification } from '../hooks/useDisconnectNotification';
import { useRejoinGame } from '../hooks/useRejoinGame';
import { ConnectionStatus } from './ui/ConnectionStatus';
import { WaitingRoom } from './ui/WaitingRoom';
import { GameBoard } from './game/GameBoard';
import { HomeScreen } from '../screens/HomeScreen';
import { RoomSelection } from '../screens/RoomSelection';
import { ResultScreen } from '../screens/ResultScreen';
import { StatsScreen } from '../screens/StatsScreen';
import { QuestScreen } from '../screens/QuestScreen';
import { CustomRoomScreen } from '../screens/CustomRoomScreen';
import { GiftCodeScreen } from '../screens/GiftCodeScreen'; 
import { FriendScreen } from '../screens/FriendScreen';
import { FriendBattleScreen } from '../screens/FriendBattleScreen';
import { FriendWaitingRoom } from './ui/FriendWaitingRoom';

type ScreenType = 'home' | 'room-selection' | 'waiting' | 'playing' | 'result' | 'stats' | 'quests' |'custom' | 'giftcode' | 'friend' | 'friendbattle';


interface OnlineGameProps {
  onSwitchToLocal?: () => void;
}

export function OnlineGame({ onSwitchToLocal }: OnlineGameProps) {
  const { socket, isConnected } = useSocket();
  const { user } = useAuth();
  const [isInRoom, setIsInRoom] = useState(false);
  const [screen, setScreen] = useState<ScreenType>('home');
  const [dailyBonusResult, setDailyBonusResult] = useState<any>(null);
  const [isFriendBattle, setIsFriendBattle] = useState(false);


  useEffect(() => {
    if (!socket || !user) return;
    
    const handleGameOver = (data: any) => {
      console.log('[OnlineGame] game_over with dailyBonus:', data.dailyBonusResults);
      
      // 
      if (data.dailyBonusResults && data.dailyBonusResults[user.id]) {
        setDailyBonusResult(data.dailyBonusResults[user.id]);
      }
    };
    
    socket.on('game_over', handleGameOver);
    
    return () => {
      socket.off('game_over', handleGameOver);
    };
  }, [socket, user?.id]);  







   useRejoinGame({ 
    socket, 
    isConnected, 
    userId: user?.id 
  });

  
  // 
  const {
    roomId,
    playerIndex,
    myHand,
    players,
    gameStatus,
    opponentHands,
    gameOverData,
    resetGameState,
  } = useOnlineGameState({ socket });

  const {
    roundResult,
    scores,
    wins,
    isShowdown,
  } = useRoundJudge({ socket });

  const {
    currentMultiplier,
    fieldCards,
    playerSelections,
    setTurnIndex,
    timeRemaining,
    timeLimit,
  } = useTurnFlow({ socket });

  //selectedCardIndex 
  const { playCard, selectedCardIndex, jokerError } = useCardPlay({
    socket,
    roomId,
    playerIndex,
    myHand,
    playerSelections,
    setTurnIndex,
  });

  const { warnings, removeWarning } =useWarnings({ socket });
  const { notification } = useDisconnectNotification({ socket });

  // 
  const handleNavigate = (type: 'local' | 'multi' | 'custom' | 'friend' | 'stats' | 'quests' | 'giftcode' | 'friendbattle') => {
    if (type === 'multi') {
      setScreen('room-selection');
    } else if (type === 'custom') {
      setScreen('custom');
    } else if (type === 'friend') {
      setScreen('friend');
    } else if (type === 'friendbattle') {
    setScreen('friendbattle');
    } else if (type === 'local') {
      if (onSwitchToLocal) {
        onSwitchToLocal();
      }
    }
     else if (type === 'quests') {
    setScreen('quests');
    }
    else if (type === 'stats') { 
      setScreen('stats');
    }
     else if (type === 'giftcode') {
    setScreen('giftcode');
  }
  };



// 
  const handleRoomJoined = (isFriend = false) => {
  setIsInRoom(true);
  setIsFriendBattle(isFriend);
  setScreen('waiting');
};

  // 
  const handleBackToHome = () => {
    setScreen('home');
    setIsInRoom(false);
  };


//rejoin_success  isInRoom  true 
useEffect(() => {
  if (!socket) return;

  //
  socket.on('rejoin_waiting_success', (data) => {
    console.log('[OnlineGame] rejoin_waiting_success:', data);
    setIsInRoom(true);
    setScreen('waiting');
    
    // status 
    localStorage.setItem('kadoma_active_room_status', 'waiting');
  });

  socket.on('rejoin_success', (data) => {
  console.log('[OnlineGame] rejoin_success - setting isInRoom to true');
  console.log('[OnlineGame] rejoin_success data:', data);
  setIsInRoom(true);
  setScreen('playing');
});

  return () => {
    socket.off('rejoin_success');
  };
}, [socket]);



  //
  const handleReturnHome = () => {
    console.log('[OnlineGame] Returning home, resetting game state');
    resetGameState(); 
    setIsInRoom(false);
    setScreen('home');
  };


  //
  const handleRematch = () => {
    if (!socket) return;
   const userId = user?.id || localStorage.getItem('kadoma_user_id');
   const playerName = user?.username 
    || localStorage.getItem('kadoma_username') 
    || 'Player';
   console.log('[OnlineGame] handleRematch - gameOverData:', gameOverData);
  console.log('[OnlineGame] handleRematch - roomConfig:', gameOverData?.roomConfig);
  if (gameOverData?.roomConfig) {
    console.log('[OnlineGame] Sending join_multi_room with roomId:', gameOverData.roomConfig.id);
    // 
    socket.emit('join_multi_room', { 
      roomId: gameOverData.roomConfig.id,
      userId, 
      username: playerName 
    }, (response: any) => {
      console.log('[OnlineGame] join_multi_room response:', response);
      if (response.success) {
        setIsInRoom(true);
        setScreen('waiting');
      } else {
       if (response.shortage) {
          alert(
            `\n\n` +
            `: ${response.required?.toLocaleString()} G\n` +
            `: ${response.current?.toLocaleString()} G\n` +
            `: ${response.shortage?.toLocaleString()} G`
          );
        } else {
          alert(response.error || '');
        }
        
        // 
        setIsInRoom(false);
        setScreen('home');
      }
    });
  } else {
    // 
    socket.emit('join_room', { playerName, userId });
    setIsInRoom(true);
    setScreen('waiting');
  }
      // gameStatus  'waiting' 
};
  

  // UI
  if (!isConnected) {
    return <ConnectionStatus />;
  }

  // 
  if (screen === 'friend') {
    return <FriendScreen onBack={handleBackToHome} />;
  }

// 
if (screen === 'friendbattle') {
  return <FriendBattleScreen onBack={handleBackToHome} onRoomJoined={() => handleRoomJoined(true)} />;
}

  // 
if (screen === 'giftcode') {
  return <GiftCodeScreen onBack={handleBackToHome} />;
}
  // 
if (screen === 'quests') {
  return <QuestScreen onClose={handleBackToHome} />;
}
  // 
  if (screen === 'stats') {
    return <StatsScreen onBack={handleBackToHome} />;
  }

    // 
  if (screen === 'room-selection') {
    return (
      <RoomSelection 
        onBack={handleBackToHome} 
        onRoomJoined={handleRoomJoined}
      />
    );
  }

  // 
if (screen === 'custom') {
  return (
    <CustomRoomScreen
      onBack={handleBackToHome}
      onRoomJoined={handleRoomJoined}
    />
  );
}
  
  //  
  if (!isInRoom) {
    return <HomeScreen onNavigate={handleNavigate} />;
  }

  //  
  if (gameStatus === 'finished' && gameOverData) {
    return (
      <ResultScreen
        playerIndex={playerIndex}
        finalScores={gameOverData.finalScores}
        winner={gameOverData.winner}
        players={players}
        reason={gameOverData.reason}
        buyIn={gameOverData.roomConfig?.requiredChips}
        onReturnHome={handleReturnHome}
        onRematch={handleRematch}
        dailyBonusResult={dailyBonusResult}
        isFriendBattle={isFriendBattle}
      />
    );
  }

 if (gameStatus === 'playing') {

  return (
    <GameBoard
      isConnected={isConnected}
      playerIndex={playerIndex}
      currentMultiplier={currentMultiplier}
      setTurnIndex={setTurnIndex}
      playerSelections={playerSelections}
      players={players}
      scores={scores}
      wins={wins}
      fieldCards={fieldCards}
      roundResult={roundResult}
      myHand={myHand}
      playCard={playCard}
      isShowdown={isShowdown}
      selectedCardIndex={selectedCardIndex}
      warnings={warnings}
      removeWarning={removeWarning}
      opponentHands={opponentHands}
      timeRemaining={timeRemaining}
      timeLimit={timeLimit}
      disconnectNotification={notification}
      jokerError={jokerError}
    />
  );
}

  // 
  if (gameStatus === 'waiting') {
    // 
    if (isFriendBattle) {
      return <FriendWaitingRoom onCancel={handleBackToHome} />;
    }
    // 
    return <WaitingRoom onCancel={handleBackToHome} />;
  }
  
  // 
  return <WaitingRoom onCancel={handleBackToHome} />;
}