// src/online/screens/FriendBattleScreen.tsx
import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useSocket } from '../contexts/SocketContext';

interface Friend {
  id: string;
  friendId: string;
  username: string;
  playerId: number;
}

interface FriendRoom {
  id: string;
  roomCode: string;
  creatorId?: string;
  creatorUsername?: string;
  creatorPlayerId?: number;
  config: {
    roomName: string;
    ante: number;
    jokerCount: number;
    timeLimit: number;
    maxPlayers: number;
  };
  invitedFriends?: string[];
  createdAt: string;
}

interface FriendBattleScreenProps {
  onBack: () => void;
  onRoomJoined: () => void;
}

const JOKER_MULTIPLIER_MAP: Record<number, number> = {
  3: 300,
  5: 700,
  8: 1000,
  10: 1300,
};

export function FriendBattleScreen({ onBack, onRoomJoined }: FriendBattleScreenProps) {
  const { user } = useAuth();
  const { socket } = useSocket();
  const [activeTab, setActiveTab] = useState<'create' | 'invited'>('create');
  const [friends, setFriends] = useState<Friend[]>([]);
  const [invitedRooms, setInvitedRooms] = useState<FriendRoom[]>([]);
  const [myRooms, setMyRooms] = useState<FriendRoom[]>([]);
  const [message, setMessage] = useState<{ type: 'success' | 'error'; text: string } | null>(null);

  // 
  const [ante, setAnte] = useState(1000);
  const [jokerCount, setJokerCount] = useState(2);
  const [timeLimit, setTimeLimit] = useState(30);
  const [selectedFriends, setSelectedFriends] = useState<string[]>([]);
  const anteMultiplier = JOKER_MULTIPLIER_MAP[jokerCount] || 100;



  // 
  const fetchFriends = async () => {
    if (!user) return;

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/api/friend/list/${user.id}`
      );
      const data = await response.json();

      if (data.success) {
        setFriends(data.friends);
      }
    } catch (error) {
      console.error('[FriendBattle] Failed to fetch friends:', error);
    }
  };

  // 
  const fetchInvitedRooms = async () => {
    if (!user) return;

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/api/friend-room/invited/${user.id}`
      );
      const data = await response.json();

      if (data.success) {
        setInvitedRooms(data.rooms);
      }
    } catch (error) {
      console.error('[FriendBattle] Failed to fetch invited rooms:', error);
    }
  };

  // 
  const fetchMyRooms = async () => {
    if (!user) return;

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/api/friend-room/created/${user.id}`
      );
      const data = await response.json();

      if (data.success) {
        setMyRooms(data.rooms);
      }
    } catch (error) {
      console.error('[FriendBattle] Failed to fetch my rooms:', error);
    }
  };

  // 
  useEffect(() => {
    fetchFriends();
    fetchInvitedRooms();
    fetchMyRooms();
  }, [user]);

  // 
  const toggleFriendSelection = (friendId: string) => {
    setSelectedFriends(prev =>
      prev.includes(friendId)
        ? prev.filter(id => id !== friendId)
        : [...prev, friendId]
    );
  };

  // 
  const handleCreateRoom = async () => {
    if (!user) return;

    if (selectedFriends.length === 0) {
      showMessage('error', '1');
      return;
    }

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/api/friend-room/create`,
        {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({
            userId: user.id,
            config: {
              roomName: `${user.username}`,
              ante,
              jokerCount,
              timeLimit,
              anteMultiplier,
              invitedFriends: selectedFriends
            }
          })
        }
      );

      const data = await response.json();

      if (data.success) {
        showMessage('success', '');
        
        // 
        setSelectedFriends([]);
        
        // 
        fetchMyRooms();
        
         // Socket.io 
        if (socket) {
          socket.emit('join_friend_room', {
            roomId: data.room.id,
            userId: user.id,
            username: user.username
          }, (response: any) => {
            console.log('[FriendBattle] Join response:', response);
            
            if (response.success) {
              // 
              onRoomJoined();
            } else {
              showMessage('error', response.error || '');
            }
            });
          }
          } else {
              showMessage('error', data.error || '');
          }
          } catch (error) {
              showMessage('error', '');
              console.error('[FriendBattle] Create room error:', error);
          }
          };

  // 
  const handleDeleteRoom = async (roomId: string) => {
    if (!user) return;

    try {
      const response = await fetch(
        `${process.env.REACT_APP_API_URL}/api/friend-room/${roomId}`,
        {
          method: 'DELETE',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ userId: user.id })
        }
      );

      const data = await response.json();

      if (data.success) {
        showMessage('success', '');
        fetchMyRooms();
      } else {
        showMessage('error', data.error || '');
      }
    } catch (error) {
      showMessage('error', '');
      console.error('[FriendBattle] Delete room error:', error);
    }
  };

  // 
  const showMessage = (type: 'success' | 'error', text: string) => {
    setMessage({ type, text });
    setTimeout(() => setMessage(null), 3000);
  };


  // 
  const handleJoinInvitedRoom = (roomId: string) => {
    if (!user || !socket) return;
    socket.emit('join_friend_room', {
      roomId,
      userId: user.id,
      username: user.username
    }, (response: any) => {
      console.log('[FriendBattle] Join invited room response:', response);
      
      if (response.success) {
        // 
        onRoomJoined();
      } else {
        showMessage('error', response.error || '');
      }
    });
  };



  return (
    <div className="min-h-screen bg-gradient-to-b from-gray-900 to-gray-800 p-4">
      <div className="max-w-2xl mx-auto">
        {/*  */}
        <div className="flex items-center justify-between mb-6">
          <button
            onClick={onBack}
            className="px-4 py-2 bg-gray-700 hover:bg-gray-600 text-white rounded-lg transition"
          >
             
          </button>
          <h1 className="text-2xl font-bold text-white"></h1>
          <div className="w-20"></div>
        </div>

        {/*  */}
        {message && (
          <div
            className={`mb-4 p-4 rounded-lg ${
              message.type === 'success'
                ? 'bg-green-600 text-white'
                : 'bg-red-600 text-white'
            }`}
          >
            {message.text}
          </div>
        )}

        {/*  */}
        <div className="flex gap-2 mb-6">
          <button
            onClick={() => setActiveTab('create')}
            className={`flex-1 py-3 font-bold rounded-lg transition ${
              activeTab === 'create'
                ? 'bg-orange-600 text-white'
                : 'bg-gray-700 text-gray-400 hover:bg-gray-600'
            }`}
          >
            
          </button>
          <button
            onClick={() => setActiveTab('invited')}
            className={`flex-1 py-3 font-bold rounded-lg transition relative ${
              activeTab === 'invited'
                ? 'bg-orange-600 text-white'
                : 'bg-gray-700 text-gray-400 hover:bg-gray-600'
            }`}
          >
             ({invitedRooms.length})
            {invitedRooms.length > 0 && (
              <span className="absolute -top-1 -right-1 bg-red-500 text-white text-xs rounded-full w-5 h-5 flex items-center justify-center">
                {invitedRooms.length}
              </span>
            )}
          </button>
        </div>

        {/*  */}
        {activeTab === 'create' && (
          <div className="space-y-6">
            {/*  */}
            <div className="bg-gray-800 rounded-lg p-6">
              <h2 className="text-white font-bold mb-4"></h2>
              
              <div className="space-y-4">

                {/*  */}
                <div>
                  <label className="block text-gray-400 text-sm mb-2">
                    : {ante.toLocaleString()} G
                  </label>
                  <input
                    type="range"
                    min="100"
                    max="10000"
                    step="100"
                    value={ante}
                    onChange={(e) => setAnte(parseInt(e.target.value))}
                    className="w-full"
                  />
                </div>

                {/* JOKER */}
          <div>
            <label className="block text-white font-bold mb-2">JOKER</label>
            <select
              value={jokerCount}
              onChange={(e) => setJokerCount(Number(e.target.value))}
              className="w-full px-4 py-3 bg-gray-700 text-white rounded-lg focus:outline-none focus:ring-2 focus:ring-purple-500"
            >
              <option value={3}>3</option>
              <option value={5}>5</option>
              <option value={8}>8</option>
              <option value={10}>10</option>
            </select>
          </div>

                 {/*  */}
                <div>
                  <label className="block text-gray-400 text-sm mb-2"></label>
                  <div className="w-full px-4 py-3 bg-gray-700 text-white text-xl font-bold rounded-lg border-2 border-gray-600">
                    {anteMultiplier}
                  </div>
                  <p className="text-gray-500 text-xs mt-1"> JOKER</p>
                </div>



                {/*  */}
                <div>
                  <label className="block text-gray-400 text-sm mb-2">
                    : {timeLimit}
                  </label>
                  <input
                    type="range"
                    min="10"
                    max="60"
                    step="5"
                    value={timeLimit}
                    onChange={(e) => setTimeLimit(parseInt(e.target.value))}
                    className="w-full"
                  />
                </div>
              </div>
            </div>

            {/*  */}
            <div className="bg-gray-800 rounded-lg p-6">
              <h2 className="text-white font-bold mb-4">
                 ({selectedFriends.length})
              </h2>
              
              {friends.length === 0 ? (
                <p className="text-gray-400 text-center py-8">
                  
                </p>
              ) : (
                <div className="space-y-2">
                  {friends.map((friend) => (
                    <button
                      key={friend.friendId}
                      onClick={() => toggleFriendSelection(friend.friendId)}
                      className={`w-full p-4 rounded-lg transition ${
                        selectedFriends.includes(friend.friendId)
                          ? 'bg-orange-600 text-white'
                          : 'bg-gray-700 text-gray-300 hover:bg-gray-600'
                      }`}
                    >
                      <div className="flex items-center justify-between">
                        <div>
                          <p className="font-bold">{friend.username}</p>
                          <p className="text-sm opacity-75">ID: {friend.playerId}</p>
                        </div>
                        {selectedFriends.includes(friend.friendId) && (
                          <span className="text-2xl"></span>
                        )}
                      </div>
                    </button>
                  ))}
                </div>
              )}
            </div>

            {/*  */}
            <button
              onClick={handleCreateRoom}
              disabled={selectedFriends.length === 0}
              className="w-full py-4 bg-orange-600 hover:bg-orange-700 disabled:bg-gray-600 text-white font-bold text-xl rounded-lg transition"
            >
              
            </button>

            {/*  */}
            {myRooms.length > 0 && (
              <div className="bg-gray-800 rounded-lg p-6">
                <h2 className="text-white font-bold mb-4"></h2>
                <div className="space-y-3">
                  {myRooms.map((room) => (
                    <div
                      key={room.id}
                      className="bg-gray-700 rounded-lg p-4"
                    >
                      <div className="flex items-center justify-between mb-2">
                        <p className="text-white font-bold">{room.config.roomName}</p>
                        <p className="text-gray-400 text-sm">: {room.roomCode}</p>
                      </div>
                      <div className="flex items-center justify-between text-sm text-gray-400 mb-3">
                        <span>: {room.config.ante.toLocaleString()} G</span>
                        <span>JOKER: {room.config.jokerCount}</span>
                        <span>: {room.config.timeLimit}</span>
                      </div>
                      <button
                        onClick={() => handleDeleteRoom(room.id)}
                        className="w-full py-2 bg-red-600 hover:bg-red-700 text-white rounded-lg transition"
                      >
                        
                      </button>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        )}

                {/*  */}
        {activeTab === 'invited' && (
          <div className="space-y-6">
            {invitedRooms.length === 0 ? (
              <div className="bg-gray-800 rounded-lg p-12 text-center">
                <p className="text-gray-400 text-lg"></p>
              </div>
            ) : (
              <div className="space-y-3">
                {invitedRooms.map((room) => (
                  <div
                    key={room.id}
                    className="bg-gray-800 rounded-lg p-6"
                  >
                    <div className="flex items-center justify-between mb-4">
                      <div>
                        <h3 className="text-white font-bold text-lg">
                          {room.config.roomName}
                        </h3>
                        <p className="text-gray-400 text-sm">
                          : {room.creatorUsername} (ID: {room.creatorPlayerId})
                        </p>
                      </div>
                      <div className="text-right">
                        <p className="text-gray-400 text-xs"></p>
                        <p className="text-white font-mono text-xl">{room.roomCode}</p>
                      </div>
                    </div>

                    <div className="grid grid-cols-3 gap-4 mb-4 text-sm">
                      <div>
                        <p className="text-gray-400"></p>
                        <p className="text-white font-bold">{room.config.ante?.toLocaleString()} G</p>
                      </div>
                      <div>
                        <p className="text-gray-400">JOKER</p>
                        <p className="text-white font-bold">{room.config.jokerCount}</p>
                      </div>
                      <div>
                        <p className="text-gray-400"></p>
                        <p className="text-white font-bold">{room.config.timeLimit}</p>
                      </div>
                    </div>

                    {/*   */}
                    <button
                      onClick={() => handleJoinInvitedRoom(room.id)}
                      className="w-full py-3 bg-orange-600 hover:bg-orange-700 text-white font-bold rounded-lg transition"
                    >
                      
                    </button>
                  </div>
                ))}
              </div>
            )}
          </div>
        )}
      </div>
    </div>
  );
}