// shared/game/joker.js
// 
//  

const { MAX_JOKER_COUNT, MIN_POINTS } = require('../config');

/**
 * 
 */
function isJoker(card) {
  return card.rank === 'JOKER1' || card.rank === 'JOKER2';
}

/**
 * 
 */
function hasJoker(cards) {
  return cards.some(isJoker);
}

/**
 * 
 */
function checkJokerInHands(players) {
  console.log('[checkJokerInHands] :', players.length);
  
  const result = players.some(player => {
    const playerHasJoker = hasJoker(player.hand);
    console.log(`[checkJokerInHands] ${player.name} :`, player.hand.map(c => c.rank));
    console.log(`[checkJokerInHands] ${player.name} JOKER:`, playerHasJoker);
    return playerHasJoker;
  });
  
  console.log('[checkJokerInHands] :', result);
  return result;
}

/**
 * 
 */
function checkJokerInField(fieldCards) {
  const validCards = fieldCards.filter(card => card !== null);
  return hasJoker(validCards);
}

/**
 * 
 */
function countJokers(cards) {
  return cards.filter(isJoker).length;
}

/**
 * 
 */
function shouldReshuffleAfterSet(jokerDealtThisSet) {
  console.log('[shouldReshuffleAfterSet] jokerDealtThisSet:', jokerDealtThisSet);
  return jokerDealtThisSet;
}

/**
 * JOKER
 * @param {Object} card - 
 * @param {number} setTurnIndex - 0~4
 * @returns {boolean} true
 */
function canPlayJoker(card, setTurnIndex) {
  // JOKER
  if (!isJoker(card)) {
    return true;
  }
  
  // JOKER1setTurnIndex === 0
  return setTurnIndex !== 0;
}

/**
 * 0
 */
function hasPlayerBelowMinPoints(players) {
  return players.some(player => player.points <= MIN_POINTS);
}

/**
 * 
 * @param {boolean} allHandsEmpty - 
 * @param {number} jokerCount - JOKER
 * @param {Array} players - 
 * @returns {Object}  { shouldEnd, reason }
 */
function checkGameEnd(allHandsEmpty, jokerCount, players, maxJokerCount = MAX_JOKER_COUNT) {
  // 1: 0
  if (hasPlayerBelowMinPoints(players)) {
    const bankruptPlayer = players.find(p => p.points <= MIN_POINTS);
    const playerName = bankruptPlayer?.username || '';  //BotName
    return {
      shouldEnd: true,
      reason: `${playerName}${MIN_POINTS}`
    };
  }

  // 2:   JOKER
  if (allHandsEmpty && jokerCount >= maxJokerCount) {
    return {
      shouldEnd: true,
      reason: `JOKER${maxJokerCount}`
    };
  }

  return { shouldEnd: false };
}

// CommonJS (Node.js)
if (typeof module !== 'undefined' && module.exports) {
  module.exports = {
    isJoker,
    hasJoker,
    checkJokerInHands,
    checkJokerInField,
    countJokers,
    shouldReshuffleAfterSet,
    canPlayJoker,
    hasPlayerBelowMinPoints,
    checkGameEnd,
    MAX_JOKER_COUNT,
    MIN_POINTS
  };
}

// ES Module ()
if (typeof window !== 'undefined') {
  window.JokerLogic = {
    isJoker,
    hasJoker,
    checkJokerInHands,
    checkJokerInField,
    countJokers,
    shouldReshuffleAfterSet,
    canPlayJoker,
    hasPlayerBelowMinPoints,
    checkGameEnd,
    MAX_JOKER_COUNT,
    MIN_POINTS
  };
}